<template>
  <v-container grid-list-lg>
    <v-form ref="form" lazy-validation>
      <v-toolbar>
        <v-toolbar-title>
          <h3 v-if="isCreate">
            Create a new project
          </h3>
          <h3 v-else-if="!!project">
            Edit project: "{{project.title}}"
          </h3>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-container fill>  <!--do this to center the toggle vertically -->
            <v-tooltip bottom>
              <div slot="activator">
                <v-switch
                  v-model="project.publish"
                ></v-switch>
              </div>
              <span>Publish</span>
            </v-tooltip>
          </v-container>
          <v-btn flat @click="save()">
            <span v-if="isCreate">Create</span>
            <span v-else>Save</span>
          </v-btn>
          <v-btn flat :disabled=!!isCreate @click="delete_project()">Delete</v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-layout row wrap>
        <v-flex></v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 sm8>
          <v-card>
            <p>
              <v-card-text>
                <v-text-field
                  label="Title"
                  v-model="project.title"
                  required
                  ref="form_title"
                ></v-text-field>
              </v-card-text>
            </p>
          </v-card>
          <p>
            <v-card>
              <v-card-text>
                <v-text-field
                  label="Subtitle"
                  v-model="project.subtitle"
                  required
                  ref="form_subtitle"
                ></v-text-field>
              </v-card-text>
            </v-card>
          </p>
        </v-flex>
        <v-flex xs12 sm4>
          <p>
            <v-card>
              <v-card-text>
                <!--<v-text-field label="Client"></v-text-field>-->
                <span>Client field will go here</span>
              </v-card-text>
            </v-card>
          </p>
          <p>
            <v-card>
              <v-card-text>
                <v-menu
                  ref="dateMenu"
                  lazy
                  :close-on-content-click="false"
                  v-model="dateMenu"
                  transition="scale-transition"
                  offset-y
                  full-width
                  :nudge-right="40"
                  min-width="290px"
                  :return-value.sync="project.timestamp"
                >
                  <v-text-field
                    slot="activator"
                    label="Pick a date"
                    v-model="project.timestamp"
                    prepend-icon="event"
                    readonly
                    ref="form_timestamp"
                  ></v-text-field>
                  <v-date-picker v-model="project.timestamp" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="dateMenu = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.dateMenu.save(project.timestamp)">OK</v-btn>
                  </v-date-picker>
                </v-menu>
              </v-card-text>
            </v-card>
          </p>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 md6>
          <v-card color="purple lighten-5">
            <v-card-title class="title">
              Body (Markdown)
            </v-card-title>
            <v-card-text>
              <textarea v-model="project.body" @input="updateMarkup" class="markup-input"></textarea>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs12 md6>
          <v-card>
            <v-card-title class="title">
              Body (Rendered HTML)
            </v-card-title>
            <v-card-text>
              <div v-html="project.body_html" class="compiled-markdown"></div>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-snackbar v-model="snack.show" :color="snack.color" :multi-line=true :top=true>
        {{ snack.msg }}
        <v-btn flat  @click.native="snack.show = null">Close</v-btn>
      </v-snackbar>
    </v-form>
  </v-container>
</template>
<style>
  textarea.markup-input {
    border-color: grey;
    border: thin;
    border-style: dashed;
    width:100%;
    /*min-height:300px;*/
  }
  /*div.compiled-markdown {*/
    /*height:100%;*/
  /*}*/
</style>
<script>
  import marked from 'marked'
  import _ from 'underscore'
  import postservice from '@/services/postservice'

  export default {
    props: {
      project_id_prop: {
        type: String,
        default: null
      }
    },
    data () {
      return {
        project: {
          id: null,
          title: null,
          subtitle: null,
          body: null,
          body_html: null,
          timestamp: null,
          publish: false
        },
        snack: {
          msg: null,
          show: false,
          color: 'info'
        },
        dateMenu: false
      }
    },
    computed: {
      isCreate () {
        return !this.project_id_prop
      },
      bodyMarkdown: function () {
        return this.project.body
      }
    },
    methods: {
      save () {
        let promise = null
        if (this.isCreate) {
          promise = postservice.createProject(this.project)
        } else {
          promise = postservice.updateProject(this.project)
        }

        promise
          .then(response => {
            if (this.isCreate) {
              this.$router.push({name: 'Admin'})
            }
            this.showSnack('Saved!', 'success')
          })
          .catch(err => {
            console.error('Failed to create or update project', err)
            if (err.response.status === 422) {
              this.handle422(err.response.data)
            } else {
              this.showSnack('Failed to create or update project: ' + err.response.status, 'error')
            }
          })
      },
      delete_project () {
        postservice.deleteProject(this.project)
          .then(response => {
            console.debug('Project deleted', response.data)
            this.$router.push({name: 'Admin'})
            this.showSnack(response.msg, 'success')
          })
          .catch(err => {
            console.error('Failed to delete project', err)
            this.showSnack('Failed to delete project:' + err.response.data.message, 'error')
          })
      },
      showSnack (msg, statusColor) {
        console.log(msg)
        this.snack.msg = msg
        this.snack.show = true
        this.snack.color = statusColor
      },
      updateMarkup: _.debounce(function (e) {
        this.project.body = e.target.value
      }, 300),
      handle422: function (data) {
        if (data.hasOwnProperty('msg')) {
          this.showSnack(data.msg, 'error')
        } else {
          for (let fieldName in data) {
            for (let msg in data[fieldName]) {
              this.$refs['form_' + fieldName].errorMessages.push(data[fieldName][msg])
            }
          }
        }
      }
    },
    watch: {
      bodyMarkdown () {
        // marked can't handle nulls, give it a space just in case.
        this.project.body_html = '<div class="rendered-markdown">' + marked(this.project.body || ' ', {sanitize: true}) + '</div>'
      }
    },
    created () {
      if (!this.isCreate) {
        postservice.getProject(this.project_id_prop).then(response => {
          this.project = response.data.project
        })
      }
    }
  }
</script>



